.footer {
    background-color: var(--container-color);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}


.footer__container{
    padding: 2rem 0  3rem;
}

.footer__title,
.footer__link {
    color: #32454e;

}

.footer__title{
    text-align: center;
}

.footer__subtitle{
    display: flex;
    justify-content: center;
    margin-bottom: var(--mb-2);
}

.footer__link:hover {
    color: rgb(0, 0, 0);
    transform: scale(1.04);
    filter: drop-shadow(2px 4px 6px rgb(30, 41, 39));
}

.footer__link {
   transition: transform 330ms ease-in-out;
}

.footer__list {
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
    font-size: 1.1rem;
    margin-bottom: var(--mb-2);
}


.footer__social{
    display: flex;
    justify-content: center;
    column-gap: 2.2rem;
}

.footer__social-link {
    background-color: var(--title-color);
    color: var(--container-color);
    font-size: 1.2rem;
    padding: 0.4rem;
    border-radius: 10px;
    display: inline-flex;
}

.footer__social-link:hover{
    box-shadow: 1px 1px  20px #7985dfa0;
    /* background-color: #455a6449; */
    transform: scale(1.08);
     filter: drop-shadow(2px 4px 6px rgba(160, 238, 226, 0.289));
     cursor: pointer;
 }

 .footer__social-link{
    transition: transform 330ms ease-in-out;  
 }

.footer__copy{
    display: block;
    margin-top: 4.5rem;
    color: var(--title-color);
    text-align: center;
    font-size: smaller;
}


