 .skills__container {
     grid-template-columns: repeat(2, 440px);
     column-gap: 5rem;
     justify-content: center;
 }

 .section__title{
    color: #32454e;
  }

 .skills__content {
     background-color: var(--container-color);
     border: 1px solid rgba(0, 0, 0, 0.1);
     border-radius: 20px;
     padding: 2rem 4rem;
     border-bottom: 1.25rem;
 }


 .skills__title {
     font-size: x-large;
     font-weight: 600;
     text-align: center;
     margin-bottom: var(--mb-1-5);
 }


 .skills__box{
     display: flex;
     justify-content: center;
     column-gap: 2rem;
 }

 .skills__content:hover{
    box-shadow: 1px 1px  20px #2121215a;
    /* background-color: #455a6449; */
    transform: scale(1.02);
     filter: drop-shadow(2px 4px 6px rgba(160, 238, 226, 0.289));
     cursor: pointer;

 }

 .skills__content{
    transition: transform 330ms ease-in-out;
    
 }

 .skills__group {
     display: grid;
     align-items: flex-start;
     row-gap: 3.5rem;
 }

 .skills__data {
     display: flex;
     column-gap: 1rem;
 }

 .bx-badge-check {
     font-size: 1rem;
     color: black;
 }

 .skills__name {
    font-size: large;
    font-weight: 500;
    line-height: 18px;
 }

 .skills__level{
    font-size: 14px;
 }


 
/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .skills__container {
        grid-template-columns: max-content;
        row-gap: 2rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 576px) {
    .skills__container{
        grid-template-columns: 1fr;
    }

    .skills__content{
        padding: 1.5rem;
    }
  }
  
  /* For small devices */
  @media screen and (max-width: 350px) {
   .skills__box{
    column-gap: 1.25rem;
   }

   .skills__name {
    font-size: smaller;
   }
  }
  