.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: black;
}

.nav{
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav__logo,
.nav__toggle{
    color: white;
    font-weight: var(--font-medium);
}


.nav__logo:hover,
.nav__toggle:hover {
    filter: drop-shadow(4px 4px 8px rgb(255, 255, 255));
    transform: skewX(-0.06turn);
}

.nav__list {
    display: flex;
    column-gap: 2rem;
}


.nav__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--medium-font-size);
    color: white;
    font-weight: var(--font-medium);
    transition: .3s;
}

.nav__icon,
.nav__close,
.nav__toggle{
    display: none;
}

.active-link,
.nav__link:hover{
    color: rgba(145, 220, 230, 0.834);
    filter: drop-shadow(2px 4px 6px white);
    transform: translateY(-5px);
}



/* ===================== BreakPoints ========================= */
/* For Medium devices */

@media screen and (max-width: 768px){
    .header {
        top: initial;
        bottom: 0;
    }

    .nav {
        height: var(--header-height);
    }

    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;
        background-color: black;
    }

    /* show menu */
    .show-menu{ 
        bottom: 0;
    }

    .nav__list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }
    .nav__icon{
        font-size: 1.2rem;
    }

    .nav__close{
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: white;
    }

    .nav__close:hover{
        color: red;
    }

    .nav__toggle {
        font-size: 1.1rem;
        cursor: pointer;
    }


    .nav__icon,
    .nav__close,
    .nav__toggle{
        display: block;
    }
}

/* for small devices */
@media screen and (max-width: 350px) {
    .nav__menu{
        padding: 2rem 0.25rem 4rem;
    }

    .nav__list{
        column-gap: 0;
    }
}
