 .home__container {
     row-gap: 5rem;
 }

 .home__content {
     grid-template-columns: 116px repeat(2, 1fr);
     padding-top: 6.5rem;
     column-gap: 2rem;
     align-items: center;
 }

 .home__social {
     display: grid;
     grid-template-columns: max-content;
     row-gap: 1rem;
 }


 .home__social-icon {
     font-size: 1.8rem;
     color: #455a64;
 }

 .home__social-icon:hover {
     color: rgb(0, 0, 0);
     transform: scale(1.04);
     filter: drop-shadow(2px 4px 6px rgba(136, 224, 211, 0.875));
 }

 .home__social-icon{
    transition: transform 330ms ease-in-out;

 }


 .home__title {
     color: #32454e;
     font-size: 37px;
     margin-bottom: var(--mb-0-25);
 }


 .home__hand {
     width: 30px;
     height: 30px;
     margin-left: 0.4rem;
 }

 .home__subtitle {
     position: relative;
     font-size: large;
     color: rgba(0, 0, 0, 0.799);
     padding-left: 5.6rem;
     font-weight: var(--font-normal);
     margin-bottom: var(--mb-1);
 }

 .home__subtitle::before {
     content: '';
     position: absolute;
     width: 80px;
     height: 1px;
     background-color: black;
     left: 0;
     top: .9rem;
 }

 .home__subtitle::after {
     content: '';
     position: absolute;
     width: 80px;
     height: 1px;
     background-color: black;
     right: 35px;
     top: .9rem;
 }

 .home__description {
     color: rgba(0, 0, 0, 0.816);
     max-width: 400px;
     margin-bottom: 30px;
 }




 .home__img {
     background: url(../assets//profile.png);
     background-repeat: no-repeat;
     background-position: center;
     background-size: cover;
     border: 2px solid #00000096;
     box-shadow: inset 0 0 0 9px rgba(255, 255, 255 / 30%);
     order: 1;
     justify-self: center;
     width: 300px;
     height: 300px;
     animation: profile 6s ease-in-out infinite 1s;
 }

 @keyframes profile {
     0% {
         border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
     }

     30% {
         border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
     }

     50% {
         border-radius: 50% 40% 60% 30%/40% 40% 60% 50%;
     }

     80% {
         border-radius: 30% 60% 40% 60%/70% 30% 50% 40%;
     }

     100% {
         border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
     }

 }

 .home__scroll {
     margin-left: 9.25rem;
 }

 .wheel {
     animation: scroll 2s ease infinite;
 }

 @keyframes scroll {
     0% {
         transform: translateY(0);
     }

     30% {
         transform: translateY(3.75rem);
     }

 }

 .home__scroll-name {
     color: #000000;
     font-weight: var(--font-medium);
     margin-right: var(--mb-0-25);
     margin-left: 2px;
 }

 .home__scroll-arrow {
     font-size: 1.25rem;
     color: #000000;
 }



 /*=============== BREAKPOINTS ===============*/
 /* For large devices */
 @media screen and (max-width: 992px) {
     .home__content {
         grid-template-columns: 100px repeat(2, 1fr);
         column-gap: 1rem;
     }

     .home__hand {
         width: 22px;
         height: 22px;
     }

     .home__subtitle {
         padding-left: 4.25rem;
         margin-bottom: var(--mb-1);
     }

     .home__subtitle::before {
         width: 42px;
         top: 0.8rem;
     }

     .home__subtitle::after {
         width: 50px;
         display: none;
         top: 0.8rem;
     }

     .home__description {
         max-width: initial;
         margin-bottom: var(--mb-2-5);
     }

     .home__img {
         width: 250px;
         height: 250px;
         box-shadow: inset 0 0 0 8px rgba(255, 255, 255 / 30%);
     }

     .home__scroll {
         margin-left: 7.5rem;
     }
 }


 /* For medium devices */
 @media screen and (max-width: 768px) {
     .home__content {
         display: block;
         grid-template-columns: 0.5fr 2fr;
         padding-top: 0.5rem;
         text-align: -webkit-center;
     }

     .home__social {
         display: flex;
         justify-content: space-evenly;
     }


     .home__title {
         text-align: center;
     }


     .home__description {
         text-align: center;
     }

     .home__subtitle {
         text-align: center;
         padding: inherit;
     }

     .home__subtitle::before {
         display: none;
     }
     
     .section {
        text-align: center;
        padding: 2rem 0 4rem;
    }

     .home__img {
         order: initial;
         justify-self: initial;
         width: 267px;
        height: 223px;

     }

     .home__data {
         grid-column: 1/2;
     }

     .home__img {
         box-shadow: inset 0 0 0 6px rgba(255, 255, 255 / 30%);
         width: 200px;
         height: 200px;
     }

     .home__scroll {
         display: none;
     }


 }

 /* For small devices */
 @media screen and (max-width: 390px) {
     .home__content {
         display: block;
         grid-template-rows: 7.5fr 0fr;
         padding-top: 1.5rem;
         text-align: -webkit-center;
     }

     .home__social {
         display: flex;
         justify-content: space-evenly;
     }

     .home__img {
         width: 180px;
         height: 180px;
     }

     .home__subtitle::before {
         display: none;
     }

     .home__subtitle {
         padding-left: 1.25rem;
         margin-bottom: var(--mb-1);
     }


 }