.work__filters {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 0.75rem;
    margin-bottom: 20px;
}

.section__title{
    color: #32454e;
  }

.work__item {
    cursor: pointer;
    color: rgba(0, 0, 0, 0.777);
    padding: 0.25rem 0.75rem;
    font-weight: 200;
    border-radius: 0.5rem;
    text-transform: capitalize;
}

.work__item:hover {
    background-color: var(--title-color);
    transform: scale(1.05);
    color: white;
}

.work__container {
    grid-template-columns: repeat(3, max-content);
    gap: 3rem;
    justify-content: center;
}

.work__card {
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1.25rem;
    border-radius: 20px;
    text-align: center;
}

.work__card:hover {
    box-shadow: 1px 1px 20px #2121215a;
    /* background-color: #455a6449; */
    transform: scale(1.02);
    filter: drop-shadow(2px 4px 6px rgba(160, 238, 226, 0.289));
    cursor: pointer;

}

.work__card {
    transition: transform 330ms ease-in-out;

}

.work__img {
    width: 255px;
    border-radius: 20px;
    margin-bottom: 10px;
}

.work__title {
    font-size: x-large;
    font-weight: 600;
    text-align: center;
    margin-bottom: 5px;
}

.work__button {
    color: black;
    font-size: 13px;
}

.work__button:hover {
    color: rgba(0, 0, 255, 0.781);
}

.work__button-icon {
    font-size: 16px;
    transition: 0.4s;
    display: block;
}

.work__button:hover .work__button-icon {
    transform: translateX(0.25rem);
    color: #0000ffc7;
}

/* active */
.active-work{
    background-color: black;
    color: white;
}



/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .work__container{
        gap: 1.25rem;
    }

    .work__card{
        padding: 1rem;
    }

    .work__img{
        margin-bottom: 0.75rem;
    }

    .work__title{
        margin-bottom: 0.25rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 768px) {
    .work__container{
        grid-template-columns: max-content;
    }
  }
  
  @media screen and (max-width: 576px) {
    .work__container{
        grid-template-columns: 1fr;
    }

    .work__img{
        width: 100%;
    }
  }
  
  /* For small devices */
  @media screen and (max-width: 350px) {
    .work__item{
        font-size: var(--small-font-size);
    }

    .work__filters{
        column-gap: 0.25rem;
    }
  }
  











