 .about__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    column-gap: 6rem;
 }

 .section__title{
   color: #32454e;
 }

 .about__img {
    width: 350px;
    border-radius: 1.8rem;
    justify-self: center;
 }

 .about__img:hover {
    box-shadow: 1px 1px 20px #455a64;
    transform: scale(1.01);
    cursor: pointer;
     filter: drop-shadow(2px 4px 6px rgba(136, 224, 211, 0.66));

 }

 .about__img {
    transition: transform 330ms ease-in-out;
 }

 .about__info {
    grid-template-columns: repeat(3, 145px);
    gap: 2rem;
    margin-bottom: var(--mb-2);
 }

 .about__box {
    background-color: var(--container-color);
    cursor: pointer;
    opacity: 0.6;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.75rem;
    text-align: center;
    padding: 1rem 1.25rem;
 }

 .about__box:hover {
    opacity: 2;
    filter: drop-shadow(2px 4px 6px rgba(31, 30, 30, 0.216));
 }

 .about__icon {
    font-size: 1.5rem;
    color: rgba(0, 0, 0, 0.949);
    opacity: 2px;
    margin-bottom: 8px;
 }

 .about__title {
    font-size: medium;
    font-weight: 200px;
 }

 .about__subtitle {
    font-size: small;
 }

 .about__description {
    padding: 10px;
    margin-bottom: 20px;
 }




 /*=============== BREAKPOINTS ===============*/
 /* For large devices */
 @media screen and (max-width: 992px) {
    .about__container {
       grid-template-columns: 1fr;
       row-gap: 2.5rem;
    }

    .about__img {
       width: 250px;
    }

    .about__box {
       padding: 0.75rem 0.5rem;
    }

    .about__data {
       text-align: center;
    }

    .about__info {
       justify-content: center;
    }

    .about__description {
       padding: 0.5rem;
       margin-bottom: 2rem;
    }

 }

 /* For medium devices */
 @media screen and (max-width: 576px) {
    .about__info {
       grid-template-columns: repeat(3, 1fr);
    }

    .about__description {
       padding: 0;
    }
 }

 /* For small devices */
 @media screen and (max-width: 450px) {
    .about__info {
       grid-template-columns: repeat(2, 1fr);
    }
 }